import {useEffect, useState} from "react";

export const useMe = (api) => {
    const [me, setMe] = useState({ login: null, id: null });

    useEffect(() => {
        if (api) {
            api.getMe().then(setMe).catch((err) => console.error('getMe failed', err));
        }
    }, [api]);

    return me;
};
