import { useEffect, useReducer } from 'react';

const initialState = {
    token: null,
    organisation: null,
    repositories: [],
    filteredTags: [],
    requiredTags: [],
    daysBeforeWarning: '1',
    daysBeforeCritical: '2',
    hoursUntilNotNew: '2',
    autoScroll: 'false',
    cdoRotation: [],
    minNbReviews: 2,
    onlyNotReviewed: 'true',
};

function reducer(state, action) {
    switch (action.type) {
        case 'update':
            if (!state.hasOwnProperty(action.key)) {
                return state;
            }
            return {
                ...state,
                [action.key]: action.value
            };
        default:
            throw new Error();
    }
}

export const useSettings = () => {
    const [state, dispatch] = useReducer(reducer, initialState);

    useEffect(() => {
        const urlParams = new URLSearchParams(window.location.search);

        urlParams.forEach((value, key) =>
            dispatch({
                type: 'update',
                key,
                value: Array.isArray(initialState[key]) ? value.split(',') : value
            })
        );
    }, []);

    return state;
};
